<template>
	<div id="withdrawl">
	<span class="title">{{$t('navBar.WithdrawalCharge')}}</span>
	 <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
 		        <el-form :inline="true" :model="formBuy" label-width="50px" label-position="left">
					<el-col :span = "24" style="padding-bottom: 0px" class="toolbar">
						<el-form-item label="BTY" >
					    	<el-input v-model="formBuy.BTYTradeFee" :placeholder="$t('PleaseService')"></el-input>
						</el-form-item>
						<el-form-item>
					    <el-button type="warning"@click="setDrawFee('BTY',formBuy.BTYTradeFee)">{{$t('confirm')}}</el-button>
						</el-form-item>
					</el-col>
					<el-col :span = "24" style="padding-bottom: 0px" class="toolbar">
						<el-form-item label="USDG" >
					    	<el-input v-model="formBuy.USDGTradeFee" :placeholder="$t('PleaseService')"></el-input>
						</el-form-item>
						<el-form-item>
					    <el-button type="warning"@click="setDrawFee('USDG',formBuy.USDGTradeFee)">{{$t('confirm')}}</el-button>
						</el-form-item>
					</el-col>
 		        </el-form>
	      </el-col> 	
	</div>

</template>
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear,dateStrSecond} from '../../common/js/common.js'

	export default{
		data(){
			return {
				formBuy:{
					BTYTradeFee:"",
					USDGTradeFee:"",
				},
			}
		},
		methods:{
			setDrawFee:function(coin,fee){

				let params = {
					coinType:coin,
					draw:fee
				}

				ajax(this,this.ApiConfig.feeConfigure.setDrawFee,params,(res) => {
					if(res.code == 200){
						this.$notify({
				            title:"提示",
				            message:res.message,
				            type:"success"
				          })
					}else{
						this.$notify({
				            title:"提示",
				            message:res.message,
				            type:"warning"
				          })
					}
				},"POST")
			},
			showDrawFee:function(){
				ajax(this,this.ApiConfig.feeConfigure.showDrawFee,{},(res) =>{
					console.log(res)
					if(res.code==200){
						this.formBuy.BTYTradeFee = res.data.BTY;
						this.formBuy.USDGTradeFee = res.data.USDG;
					}else{
						this.$notify({
				            title:"提示",
				            message:res.message,
				            type:"warning"
				          })
					}
				},"POST")
			}
		},
		mounted(){
			this.showDrawFee()
		}
	}
</script>
<style>
	#withdrawl{
		
		.el-button{
			/* width: 120px;
			height: 40px; */
			background-color: #bf9764;
			/* font-size: 16px; 
			border-radius: 6px;
			border: none;
			outline: none; */
		}
		.el-form-item__label{
			font-size: 14px;
    		color: #000;
		}

		.el-input__inner{
			font-size: 14px;
			}
}
</style>


